.header__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  position: relative;
}
.header__wrap:before {
  content: '';
  position: absolute;
  left: -39px;
  top: 0;
  width: calc(100% + 78px);
  height: 100%;
  border: 1px solid #e8e8e8;
  border-top: none;
  background-color: #fff;
  pointer-events: none;
  z-index: -1;
}
.header .logo {
  border-right: 1px solid #e8e8e8;
  width: 201px;
  height: 141px;
  line-height: 202px;
}
.header .logo svg {
  width: 157px;
  height: 93px;
}
.header__row {
  width: calc(100% - 201px);
}
.header .contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  padding: 18px 0 22px 40px;
  position: relative;
}
.header .contacts:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: calc(100% + 39px);
  height: 1px;
  background-color: #e8e8e8;
}
.header .phone {
  font-weight: 500;
  font-size: 14px;
  color: #1d1d1b;
}
.header .icons {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.header .icons a {
  background-color: #006fb9;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  margin-left: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .icons a svg {
  fill: #fff;
}
.header .icons a svg.vk {
  width: 16px;
  height: 9px;
}
.header .icons a svg.fb {
  width: 8px;
  height: 14px;
}
.header .icons a svg.ig {
  width: 15px;
  height: 15px;
  transform: translate(1px, -1px);
}
.header .menu {
  display: flex;
  list-style: none;
  font-weight: 900;
  font-size: 14px;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}
.header .menu__item {
  margin: 0 14px;
}
.header .menu__item_drop {
  position: relative;
}
.header .menu__item_drop:hover .sub {
  visibility: visible;
  opacity: 1;
}
.header .menu__item_drop > a {
  position: relative;
}
.header .menu__item_drop > a:after {
  content: '';
  position: absolute;
  right: -6px;
  top: 33px;
  width: 9px;
  height: 6px;
  background-image: url('../img/icons/arrow_dd-black.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.3s;
}
.header .menu__item:first-of-type {
  margin-left: 0;
}
.header .menu__item:first-of-type > a {
  padding-left: 40px;
}
.header .menu__item:last-of-type {
  margin-right: 0;
}
.header .menu__item:last-of-type > a {
  padding-right: 0;
}
.header .menu__item > a {
  padding: 29px 12px 26px;
  display: inline-block;
}
.header .menu .sub {
  visibility: hidden;
  opacity: 0;
  list-style: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 200px;
  font-weight: 900;
  font-size: 14px;
  margin: 0;
  padding: 20px;
  background-color: #006fb9;
  text-transform: none;
  transition: 0.3s;
  color: #fff;
}
.header .menu .sub__item:not(:last-of-type) {
  margin-bottom: 10px;
}
.header_absolute {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
}
.header_absolute .header__wrap:before {
  background-color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.header_absolute .logo {
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.header_absolute .phone {
  color: #fff;
}
.header_absolute .icons a {
  background-color: rgba(255, 255, 255, 0.1);
}
.header_absolute .contacts:after {
  background-color: rgba(255, 255, 255, 0.1);
}
.header_absolute .menu {
  color: #fff;
}
.header_absolute .menu__item_drop > a:after {
  background-image: url('../img/icons/arrow_dd.png');
}
.header_absolute .burger span {
  background-color: #fff !important;
}
.header .burger {
  position: absolute;
  display: none;
  right: 0;
  bottom: 27px;
  width: 30px;
  height: 21px;
  z-index: 6;
  border-radius: 100px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #006fb9;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  display: block;
  border-radius: 100px;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #006fb9;
}
.header .burger.active span {
  display: none;
}
.header .burger.active svg {
  display: block;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #1d1d1b;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .mobile-menu {
  background-color: #006fb9;
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  overflow: auto;
  width: 100%;
  z-index: 6;
  padding: 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu .close {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 15px;
  right: 15px;
}
.header .mobile-menu .close span {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 2px;
  background-color: #fff;
}
.header .mobile-menu .close span:first-of-type {
  transform: rotate(45deg);
}
.header .mobile-menu .close span:last-of-type {
  transform: rotate(-45deg);
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}
.header .mobile-menu .menu__item {
  border: none;
  color: #fff;
  padding: 10px 0;
  margin: 0;
  position: relative;
}
.header .mobile-menu .menu__item_drop > a:after {
  background-image: url(../img/icons/arrow_dd.png);
  top: 3px;
  right: -17px;
}
.header .mobile-menu .menu__item_drop.active > a:after {
  transform: rotate(180deg);
}
.header .mobile-menu .menu__item:first-of-type > a {
  padding-left: 0;
}
.header .mobile-menu .menu__item a {
  padding: 0;
}
.header .mobile-menu .menu .sub {
  display: none;
  transition: none;
}
